

"use client"

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { ThumbsUp, ThumbsDown, Share2, Download, ChevronLeft, ChevronRight } from "lucide-react"
import { useEffect, useState } from "react"
import { ShareModal } from "./share-modal"
import RegisterModal from "./registerModal"

interface Event {
  id: string
  title: string
  description: string
  date: string
  time: string
  location: string
  image?: string
  creatorId: string
  creatorName: string
  registeredUsers: string[]
  createdAt: string
  likedBy?: string[]
  dislikedBy?: string[]
}

const ITEMS_PER_PAGE = 6

export default function EventsList({
  events,
  userId,
  onRegister,
  onEdit,
  onDelete,
  onLike,
  onDislike,
}: {
  events: Event[]
  userId: string
  onRegister: (eventId: string) => void
  onEdit: (event: Event) => void
  onDelete: (eventId: string) => void
  onLike: (eventId: string) => void
  onDislike: (eventId: string) => void
}) {

  const [sharingEvent, setSharingEvent] = useState<Event | null>(null)
  const [currentPage, setCurrentPage] = useState(1)
  const [openEvent, setOpenEvent] = useState<Event | null>(null)
  const [registeringEvent, setRegisteringEvent] = useState<Event | null>(null)

  // ---------------------------
  //COUNTDOWN SYSTEM
  // ---------------------------

  const getCountdown = (date: string, time: string) => {
    const eventDate = new Date(`${date}T${time}`).getTime()
    const now = Date.now()
    const diff = eventDate - now

    if (diff <= 0) return "Event Started"

    const days = Math.floor(diff / (1000 * 60 * 60 * 24))
    const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60))
    const seconds = Math.floor((diff % (1000 * 60)) / 1000)

    return `${days}d ${hours}h ${minutes}m ${seconds}s`
  }

  const [countdowns, setCountdowns] = useState<{ [key: string]: string }>({})

  useEffect(() => {
    const interval = setInterval(() => {
      const updated: any = {}
      events.forEach((event) => {
        updated[event.id] = getCountdown(event.date, event.time)
      })
      setCountdowns(updated)
    }, 1000)

    return () => clearInterval(interval)
  }, [events])

  // ---------------------------

  // Register modal handlers


  const handleRegisterClick = (event: Event) => {
  setRegisteringEvent(event)
}

  const handleRegisterSubmit = (data: any) => {
    if (registeringEvent) {
      onRegister(registeringEvent.id)
      setRegisteringEvent(null)
    }
  }

  const handleRegisterModalClose = () => {
    setRegisteringEvent(null)
  }

  const sortedEvents = [...events].sort((a, b) => {
    const dateA = new Date(`${a.date}T${a.time}`).getTime()
    const dateB = new Date(`${b.date}T${b.time}`).getTime()
    return dateB - dateA
  })

  const totalPages = Math.ceil(sortedEvents.length / ITEMS_PER_PAGE)
  const startIndex = (currentPage - 1) * ITEMS_PER_PAGE
  const endIndex = startIndex + ITEMS_PER_PAGE
  const paginatedEvents = sortedEvents.slice(startIndex, endIndex)

  const downloadFlyer = (event: Event) => {
    const canvas = document.createElement("canvas")
    canvas.width = 800
    canvas.height = 600
    const ctx = canvas.getContext("2d")!

    ctx.fillStyle = "#0066ff"
    ctx.fillRect(0, 0, 800, 600)

    ctx.fillStyle = "#ffffff"
    ctx.font = "bold 48px Arial"
    ctx.textAlign = "center"
    ctx.fillText(event.title, 400, 100)

    ctx.font = "24px Arial"
    ctx.fillText(` ${new Date(event.date).toLocaleDateString()}`, 400, 200)
    ctx.fillText(` ${event.time}`, 400, 260)
    ctx.fillText(` ${event.location}`, 400, 320)

    ctx.font = "16px Arial"
    ctx.fillText(event.description.substring(0, 50), 400, 400)

    const link = document.createElement("a")
    link.href = canvas.toDataURL()
    link.download = `${event.title}-flyer.png`
    link.click()
  }

  if (events.length === 0) {
    return (
      <div className="text-center py-16">
        <p className="text-muted-foreground text-lg">No events yet. Create one to get started!</p>
      </div>
    )
  }

  return (
    <div className="space-y-6">

      {/* =======================
          EVENT GRID
      ======================= */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {paginatedEvents.map((event) => {
          const isRegistered = event.registeredUsers?.includes(userId)
          const likeCount = event.likedBy?.length || 0
          const dislikeCount = event.dislikedBy?.length || 0
          const userLiked = event.likedBy?.includes(userId)
          const userDisliked = event.dislikedBy?.includes(userId)
          const isCreator = userId === event.creatorId

          return (
            <Card
              key={event.id}
              className="overflow-hidden border-primary/20 hover:shadow-lg transition-shadow flex flex-col"
            >
              {event.image && (
                <img src={event.image || "/placeholder.svg"} alt={event.title} className="w-full h-70 object-cover" />
              )}

              <CardHeader className="pb-3 bg-primary/5">
                <CardTitle className="text-primary line-clamp-2">{event.title}</CardTitle>
                <p className="text-sm text-muted-foreground mt-1">Created by {event.creatorName}</p>
              </CardHeader>

              <CardContent className="pt-4 space-y-3 flex-grow flex flex-col">

                {/* COUNTDOWN DISPLAY */}
                <div className="mt-2 p-2 rounded bg-blue-50 text-blue-700 text-sm font-semibold text-center">
                  {countdowns[event.id] || "Loading..."}
                </div>

                <div className="grid grid-cols-2 gap-2 text-sm">
                  <div>
                    <p className="text-xs text-muted-foreground uppercase tracking-wide">Date</p>
                    <p className="font-semibold text-foreground">
                      {new Date(event.date).toLocaleDateString()}
                    </p>
                  </div>
                  <div>
                    <p className="text-xs text-muted-foreground uppercase tracking-wide">Time</p>
                    <p className="font-semibold text-foreground">{event.time}</p>
                  </div>
                </div>

                <div>
                  <p className="text-xs text-muted-foreground uppercase mb-1">Location</p>
                  <p className="text-foreground text-sm line-clamp-2">{event.location}</p>
                </div>

                {/*  CLICK TO OPEN FULL-SCREEN DETAILS */}
                <p
                  className="text-foreground text-sm line-clamp-2 flex-grow cursor-pointer hover:text-primary transition"
                  onClick={() => setOpenEvent(event)}
                >
                  {event.description}
                </p>

                {/* Buttons */}
                <div className="flex items-center gap-2 pt-3 border-t border-border">
                  <Button
                    size="sm"
                    variant="outline"
                    onClick={() => onLike(event.id)}
                    className={`flex items-center gap-1 transition ${
                      userLiked
                        ? "bg-primary/20 border-primary text-primary"
                        : "border-border text-muted-foreground hover:border-primary hover:text-primary"
                    }`}
                  >
                    <ThumbsUp className="w-4 h-4" />
                    <span>{likeCount}</span>
                  </Button>

                  <Button
                    size="sm"
                    variant="outline"
                    onClick={() => onDislike(event.id)}
                    className={`flex items-center gap-1 transition ${
                      userDisliked
                        ? "bg-destructive/20 border-destructive text-destructive"
                        : "border-border text-muted-foreground hover:border-destructive hover:text-destructive"
                    }`}
                  >
                    <ThumbsDown className="w-4 h-4" />
                    <span>{dislikeCount}</span>
                  </Button>

                  <Button
                    size="sm"
                    onClick={() => setSharingEvent(event)}
                    className="flex items-center gap-1 ml-auto bg-blue-50 text-blue-600 hover:bg-blue-100 transition"
                  >
                    <Share2 className="w-4 h-4" />
                    <span className="text-xs">Share</span>
                  </Button>
                </div>

                <div className="flex gap-2 pt-2">
                  <Button
                    size="sm"
                    onClick={() => handleRegisterClick(event)}
                    className={`flex-1 transition font-medium ${
                      isRegistered
                        ? "bg-secondary/20 text-foreground hover:bg-secondary/30"
                        : "bg-primary hover:bg-primary/90 text-primary-foreground"
                    }`}
                  >
                    {isRegistered ? " Registered" : "Register"}
                  </Button>

                  <Button
                    size="sm"
                    onClick={() => downloadFlyer(event)}
                    className="flex items-center gap-1 bg-blue-50 text-blue-600 hover:bg-blue-100 transition border border-blue-200"
                  >
                    <Download className="w-4 h-4" />
                  </Button>
                </div>

                {isCreator && (
                  <div className="flex gap-2 pt-2">
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => onEdit(event)}
                      className="flex-1 border-primary text-primary hover:bg-primary/10"
                    >
                      Edit
                    </Button>

                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => onDelete(event.id)}
                      className="flex-1 border-destructive text-destructive hover:bg-destructive/10"
                    >
                      Delete
                    </Button>
                  </div>
                )}

              </CardContent>
            </Card>
          )
        })}
      </div>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="flex items-center justify-between pt-8 border-t border-border">
          <p className="text-sm text-muted-foreground">
            Page {currentPage} of {totalPages} ({sortedEvents.length} events total)
          </p>

          <div className="flex gap-2">
            <Button
              variant="outline"
              size="sm"
              onClick={() => setCurrentPage((prev) => Math.max(1, prev - 1))}
              disabled={currentPage === 1}
              className="flex items-center gap-1"
            >
              <ChevronLeft className="w-4 h-4" />
              Previous
            </Button>

            <Button
              variant="outline"
              size="sm"
              onClick={() => setCurrentPage((prev) => Math.min(totalPages, prev + 1))}
              disabled={currentPage === totalPages}
              className="flex items-center gap-1"
            >
              Next
              <ChevronRight className="w-4 h-4" />
            </Button>
          </div>
        </div>
      )}

      {/* Share Modal */}
      {sharingEvent && (
        <ShareModal
          title={sharingEvent.title}
          url={`${typeof window !== "undefined" ? window.location.origin : ""}/event/${sharingEvent.id}`}
          onClose={() => setSharingEvent(null)}
        />
      )}

      {/* READ MODAL*/}
      
      {openEvent && (
  <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4">
    <div className="bg-white rounded-xl max-w-1xl w-full max-h-[90vh] overflow-y-auto p-6 space-y-4 shadow-xl">
      
      {/* Title */}
      <h2 className="text-2xl font-bold text-primary break-words">
        {openEvent.title}
      </h2>

      {/* Date + Time */}
      <p className="text-muted-foreground text-sm">
        {new Date(openEvent.date).toLocaleDateString()} ‚Äî {openEvent.time}
      </p>

      {/* Full Description */}
      <p className="text-lg text-foreground whitespace-pre-wrap break-words leading-relaxed">
        {openEvent.description}
      </p>

      {/* Close Button */}
      <div className ="flex  items-center justify-center">
      <Button
        className="w-[300px] mt-4"
        onClick={() => setOpenEvent(null)}
      >
        Close
      </Button>
      </div>
    
    </div>
  </div>
)}

      {registeringEvent && (
  <RegisterModal
    eventId={registeringEvent.id}
    eventTitle={registeringEvent.title}
    onClose={() => setRegisteringEvent(null)}
    onSuccess={() => {
      // Optional: Refresh registrations or show success message
      console.log('Registration submitted successfully!')
      // You can also call onRegister if you want to update local state
      // onRegister(registeringEvent.id)
    }}
  />
)}


    </div>
  )
}










